<template>
  <div>
    <v-card>
      <v-card-title>
        <span class="title font-weight-light" style="padding-right:5px;"
          >Periode</span
        >
        <span class="title font-weight-light" style="padding-right:5px;"
          >Beasiswa</span
        >
        <span class="title font-weight-light" style="padding-right:5px;"
          >Pembangunan</span
        >
        <span class="title font-weight-light">Jaya</span>
        <v-tooltip right>
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              icon
              :to="{ name: 'PeriodsCreate' }"
              v-bind="attrs"
              v-on="on"
            >
              <v-icon>
                mdi-calendar-plus
              </v-icon>
            </v-btn>
          </template>
          <span>Buat Periode</span>
        </v-tooltip>
      </v-card-title>
      <v-card-text>
        <v-data-table :headers="headers" :items="periodList">
          <template v-slot:[`item.action`]="{ item }">
            <v-tooltip left>
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  icon
                  :to="{ name: 'PeriodsEdit', params: { id: item.id } }"
                  v-bind="attrs"
                  v-on="on"
                >
                  <v-icon>
                    mdi-calendar-clock
                  </v-icon>
                </v-btn>
              </template>
              <span>Edit Periode</span>
            </v-tooltip>
            <v-tooltip left>
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  icon
                  v-bind="attrs"
                  @click="onOpenDeletePeriod(item.id)"
                  v-on="on"
                >
                  <v-icon color="red darken-4">
                    mdi-calendar-remove
                  </v-icon>
                </v-btn>
              </template>
              <span>Hapus Periode</span>
            </v-tooltip>
          </template>
        </v-data-table>
        <v-dialog v-model="dialog" persistent max-width="290">
          <v-card>
            <v-card-title class="headline">Hapus Periode</v-card-title>
            <v-card-text
              >Apakah Anda yakin ingin menghapus periode beasiswa
              ini?</v-card-text
            >
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="disable" text @click="dialog = false"
                >Kembali</v-btn
              >
              <v-btn
                :loading="loadingDeleteButton"
                color="red darken-4"
                text
                icon
                class="mr-2"
                @click="onDelete(periodId)"
                >Hapus</v-btn
              >
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-card-text>
    </v-card>
  </div>
</template>
<script>
import { mapState, mapActions } from "vuex";
import { SET_BREADCRUMB } from "@/core/services/store/breadcrumbs.module";
// import moment from "moment";
export default {
  data() {
    return {
      loadingDeleteButton: false,
      dialog: false,
      periodId: "",
      headers: [
        {
          text: "Nama Periode",
          value: "name"
        },
        {
          text: "Pembukaan Beasiswa",
          value: "start_date"
        },
        {
          text: "Batas Akhir Pengumpulan File",
          value: "due_date_file"
        },
        {
          text: "Penutupan Beasiswa",
          value: "end_date"
        },
        {
          text: "Action",
          value: "action"
        }
      ]
    };
  },
  computed: {
    ...mapState("period", ["periodList"])
  },
  async mounted() {
    this.$store.dispatch(SET_BREADCRUMB, [
      { title: "Setting", route: "alert" },
      { title: this.title }
    ]);
    await this.onFetchData();
  },
  methods: {
    ...mapActions("period", ["getPeriodList", "deletePeriod"]),
    async onFetchData() {
      await this.getPeriodList();
      // this.periodList.start_Date = moment(this.periodList.start_Date).format(
      //   "dd-MM-yyyy"
      // );
    },
    onEditPeriodeList(id) {
      this.$router.push({
        name: "periodelistDetail",
        params: { id: id }
      });
    },

    onOpenDeletePeriod(id) {
      this.dialog = true;
      this.periodId = id;
    },
    async onDelete(id) {
      try {
        this.loadingDeleteButton = true;
        await this.deletePeriod({ id: id });
        await this.onFetchData();
        this.loadingDeleteButton = false;
        this.dialog = false;
      } catch (error) {
        alert(error);
      }
    }
    // onDeleteService(id) {}
  }
};
</script>
